@import "./variables.scss";
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");
@import "./buttons.scss";

* {
  font-family: "Roboto Condensed", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
li,
dl {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  &:hover {
    text-decoration: none;
    color: inherit;
  }
}

p {
  margin: 0;
  padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.app {
  width: 1100px;
  margin: 0 auto;
  padding: 50px 0 50px 0;
  position: relative;
  .bg-decoration {
    position: absolute;
    right: -174px;
    bottom: -70px;
    z-index: -1;
  }
}

main {
  margin-top: 50px;
  position: relative;
}

.error {
  color: $main-color;
  font-weight: bold;
  margin-top: 8px;
  grid-column: 1 / 2;
  margin: 25px 0 25px 0;
}

.errorMain {
  max-width: 375px;
  color: $main-color;
  font-weight: bold;
  margin-top: 8px;
  margin: 25px 0 25px 0;
  grid-column: 1 / 3;
}

.success {
  max-width: 250px;
  color: #03710e;
  font-weight: bold;
  margin-top: 8px;
  grid-column: 1 / 2;
  margin: 25px 0 25px 0;
}

.pulse {
  animation: pulse 1.5s ease-in-out 0.5s infinite;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
